import React from 'react';

const NoMatchPage = () =>
  <main>
    <h1>Sidan kunde inte hittas.</h1>
    <p>
      Ngot mste ha gtt fel, men <b>prova grna lnkarna ovan</b> s
      lser det nog sig trots allt.
    </p>
  </main>

export default NoMatchPage;



